var string = "<% var imgURL = model.get('headshot') %>\n<img id='avatar' height='150' src=\"<%= imgURL || '/vendor/img/avatar.svg' %>\">\n\n<table>\n  <tr data-attribute='name'>\n    <th>Name:</th>\n    <td title='Double-click to edit'><span class='profile-field' id='first_name'><span class='p'><%= model.get('first_name') %></span><span class='form'><form><input name='first_name' type='text' value='<%= model.get('first_name') %>' /></form></span></span> <span class='profile-field' id='last_name'><span class='p'><%=model.get('last_name') %></span><span class='form'><form><input name='last_name' type='text' value='<%= model.get('last_name') %>' /></form></span></span></td>\n  </tr>\n  <tr data-attribute='username'>\n    <th>Username:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='username'>\n        <span class='p'><%= model.get('username') %></span>\n        <span class='form'><form><input name='username' type='text' value='<%= model.get('username') %>'></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='email'>\n    <th>E-mail:</th>\n    <td>\n      <span class='profile-field' id='email' title='Double-click to edit'>\n        <span class='p'><%= model.get('email') %></span>\n        <span class='form'><form><input name='email' type='email' value='<%= model.get('email') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='birthdate'>\n    <th>Birthdate:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='birthdate'>\n        <span class='p'><%= model.get('birthdate') || 'N/A' %></span>\n        <span class='form'><form><input name='birthdate' type='date' value='<%= model.get('birthdate') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='fach'>\n    <th>Fach:</th>\n    <td title='Double-click to edit'>\n      <% var displayFach = model.Fach ? model.Fach.displayTitle() : 'N/A' %>\n      <span class='profile-field' id='fach'>\n        <span class='p'><%= displayFach %></span>\n        <span class='form'>\n          <form>\n            <% var fach = model.escape('fach') %>\n            <select name='fach'>\n              <option<%- fach === 'Soprano - lyric coloratura' ? ' selected' : '' %>>\n                Soprano - lyric coloratura\n              </option>\n              <option<%- fach === 'Soprano - dramatic coloratura' ? ' selected' : '' %>>\n                Soprano - dramatic coloratura\n              </option>\n              <option<%- fach === 'Soprano - lyric' ? ' selected' : '' %>>\n                Soprano - lyric\n              </option>\n              <option<%- fach === 'Soprano - spinto' ? ' selected' : '' %>>\n                Soprano - spinto\n              </option>\n              <option<%- fach === 'Soprano - dramatic' ? ' selected' : '' %>>\n                Soprano - dramatic\n              </option>\n              <option<%- fach === 'Mezzo-soprano - Rossini' ? ' selected' : '' %>>\n                Mezzo-soprano - Rossini\n              </option>\n              <option<%- fach === 'Mezzo-soprano - lyric' ? ' selected' : '' %>>\n                Mezzo-soprano - lyric\n              </option>\n              <option<%- fach === 'Mezzo-soprano - dramatic' ? ' selected' : '' %>>\n                Mezzo-soprano - dramatic\n              </option>\n              <option<%- fach === 'Contralto' ? ' selected' : '' %>>\n                Contralto\n              </option>\n              <option<%- fach === 'Countertenor' ? ' selected' : '' %>>\n                Countertenor\n              </option>\n              <option<%- fach === 'Tenor leggiero' ? ' selected' : '' %>>\n                Tenor leggiero\n              </option>\n              <option<%- fach === 'Tenor - lyric' ? ' selected' : '' %>>\n                Tenor - lyric\n              </option>\n              <option<%- fach === 'Tenor - spinto' ? ' selected' : '' %>>\n                Tenor - spinto\n              </option>\n              <option<%- fach === 'Heldentenor' ? ' selected' : '' %>>\n                Heldentenor\n              </option>\n              <option<%- fach === 'Lyric baritone' ? ' selected' : '' %>>\n                Lyric baritone\n              </option>\n              <option<%- fach === 'Dramatic baritone' ? ' selected' : '' %>>\n                Dramatic baritone\n              </option>\n              <option<%- fach === 'Lyric bass' ? ' selected' : '' %>>\n                Lyric bass\n              </option>\n              <option<%- fach === 'Basso buffo' ? ' selected' : '' %>>\n                Basso buffo\n              </option>\n              <option<%- fach === 'Basso profondo' ? ' selected' : '' %>>\n                Basso profondo\n              </option>\n              <option<%- fach === 'Other' ? ' selected' : '' %>>\n                Other\n              </option>\n            </select>\n          </form>\n        </span>\n      </span>\n    </td>  </tr>\n  <tr data-attribute='city'>\n    <th>City:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='city'>\n        <span class='p'><%= model.get('city') || 'N/A' %></span>\n        <span class='form'><form><input name='city' value='<%= model.get('city') %>' /></form></span>\n      </span>\n    </td>  </tr>\n  <tr>\n    <th>Member Since:</th>\n    <td><%= model.prettyCreateDate() %></td>\n  </tr>\n</table>";

module.exports = string;
